import { Router } from 'express';
import OrderController from '../controllers/OrderController';
import validateOrder from '../middleware/orderValidation';
import Auth from '../middleware/auth';

const router = Router();

router.get('/parcels', Auth.verifyAdminToken, OrderController.getAllOrders);
router.get('/parcels/:parcelId', Auth.verifyUserToken, OrderController.getOneOrder);
router.get('/users/:userId/parcels', Auth.verifyUserToken, OrderController.getOrdersbyUser);
router.post(
  '/parcels',
  Auth.verifyUserToken,
  validateOrder.validOrder,
  OrderController.createOrder
);
router.put('/parcels/:parcelId/cancel', Auth.verifyUserToken, OrderController.cancelOrder);
router.put(
  '/parcels/:parcelId/destination',
  Auth.verifyUserToken,
  OrderController.changeOrderDestination
);
router.put('/parcels/:parcelId/status', Auth.verifyAdminToken, OrderController.changeOrderStatus);
router.put(
  '/parcels/:parcelId/presentLocation',
  Auth.verifyAdminToken,
  OrderController.changeOrderPresentLocation
);

export default router;
